@import url('https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@500&family=Kufam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@500&family=Kufam&family=Source+Code+Pro&display=swap');
*{
  margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    box-sizing: border-box;
    background: #000;

}
.main_div{
  width: 22rem;
  height: 73vh;
  display: block;
  position: absolute;
  top: 36px;
  background: #000;
  /* border: 2px solid white; */
  color: white;
}
h1{
  font-size: 3rem;
  font-family: 'Grenze Gotisch', cursive;
  font-family: 'Kufam', cursive;
}
.center_div{
  font-size: 2rem;
  /* font-family: 'Kufam', cursive; */
  color: lightgreen;
}
input{
  width: 18rem;
    margin-left: 14px;
    color: white;
    border: 3px solid;
    border-top: 0;
    border-right: 0;
    border-left: 0;
    /* border: 2px solid; */
    font-size: 2rem;
    /* color: green; */
    /* display: flex; */
    display: inline;
    border-color: lightgreen;
    /* border-radius: 14px; */
    height: 3rem;
}
button{
  display: inline;
    background: lightgreen;
    width: 42px;
    /* display: flex; */
    font-weight: bold;
    font-size: 32px;
    cursor: pointer;
    margin-left: 7px;
    height: 42px;
    color: red;
    border-radius: 50%;
}
button:hover{
  opacity:0.8;
}
.todo_style{
  flex-direction: row;
    margin-top: 14px;
}
ol{
  align-items: flex-start;
}
.todo_style i{
  display: inline;
    background: #eb3f3f;
    width: 37px;
    /* display: flex; */
    font-weight: bold;
    justify-content: center;
    padding: 5px;
    margin-right: 14px;
    font-size: 30px;
    cursor: pointer;
    margin-left: 7px;
    height: 37px;
    color: #ededed;
    border-radius: 50%;
}
.todo_style i:hover{
  opacity: 0.8;
}
li{
  font-size: 2rem;
    /* font-weight: bold; */
    font-family: 'Grenze Gotisch', cursive;
font-family: 'Kufam', cursive;
font-family: 'Source Code Pro', monospace;
    color: lightyellow;
    text-transform: capitalize;
}
@media only screen and (max-width:550px){
  .main_div{
    width: 22rem;
    height: 61vh;
    display: block;
    position: fixed;
    top: 36px;
    left: 0;
    background: #000;
    /* border: 2px solid white; */
    color: white;
  }
  .center_div{
    ont-size: 2rem;
    /* font-family: 'Kufam', cursive; */
    color: lightgreen;
  }
  h1{
    font-size: 2rem;
    width: 9rem;
  }
  input{
    width: 16rem;
    margin-left: 14px;
    color: white;
    border: 3px solid;
    border-top: 0;
    border-right: 0;
    border-left: 0;
    /* border: 2px solid; */
    font-size: 2rem;
    /* color: green; */
    /* display: flex; */
    display: inline;
    border-color: lightgreen;
    /* border-radius: 14px; */
    height: 3rem;
  }
  .todo_style{}
}